                 laws.</p> <p>This Privacy Policy governs Personal Information we collect from users (&quot;you&quot; or &quot;your&quot;)\r\n                                who visit our website\r\n                                and use\r\n                                our applications and services (&quot;Platform&quot;).</p> <p>We may revise this Privacy Policy from time to time by posting the amended terms on\r\n                                <a href=\"https://index.html/privacy-policy.html\">https://index.html/privacy-policy.html</a>.\r\n                                All amended\r\n                                terms automatically take effect immediately upon posting. Your\r\n                                continued use after an updated Privacy Policy is posted, means that you accept and\r\n                                consent to all its revisions.\r\n                            </p> <p>For the purposes of this Privacy Policy, &quot;Personal Information&quot; means any data relating\r\n                                to an identified natural\r\n                                person,\r\n                                or one who can be identified directly or indirectly by way of linking data, using\r\n                                identifiers such as name,\r\n                                voice,\r\n                                picture, identification number, online identifier, geographic location, or one or more\r\n                                special features that\r\n                                express the\r\n                                physical, psychological, economic, cultural or social identity of such person.\r\n                            </p> <p>You provide your consent to our processing of your Personal Information in accordance\r\n                                with this Privacy Policy.\r\n                            </p> <ol><li>WHO IS RESPONSIBLE FOR DATA PROCESSING &amp; HOW TO CONTACT THEM?\r\n                                    <ol><li>For the purposes of applicable data protection law, the &quot;controller&quot;\r\n                                            processing your Personal\r\n                                            Information is:<br><br>\r\n                                            Lottery Holding (&quot;Lottery&quot;, &quot;We&quot;, &quot;Us&quot; or &quot;Our&quot;).\r\n                                        </li></ol></li> <li>COLLECTION OF YOUR PERSONAL INFORMATION\r\n                                    <ol><li>The type of information We collect from you is either personal to you, or is\r\n                                            general in nature:</li> <li>Personal Information: When you register on the Platform as a member, update\r\n                                            your information,\r\n                                            purchase any goods or\r\n                                            services, take part in promotions or send emails to Us, you provide Us with\r\n                                            information that We\r\n                                            store and process. Such\r\n                                            information may include your name, address, phone number, email address,\r\n                                            credit and debit card\r\n                                            information, purchase and\r\n                                            transaction history, interests, your image, your gender, age, location,\r\n                                            nationality and other\r\n                                            similar information.</li> <li>General Information: We also receive and/or automatically collect more\r\n                                            general information from and\r\n                                            about you as a\r\n                                            result of your visits to, and use of, the Platform. This general information\r\n                                            can include information\r\n                                            such as your IP\r\n                                            address, the date and time you access the Platform, length of time you spend\r\n                                            on the Platform, your\r\n                                            browsing history\r\n                                            (recorded by the text and graphics files that compose that page), the\r\n                                            Internet address of the\r\n                                            website from which you\r\n                                            linked directly to Our Platform, and other similar information.\r\n                                        </li></ol></li> <li>COLLECTING AND USING PERSONAL INFORMATION\r\n                                    <ol><li>The Personal Information We collect from you in the course of rendering Our\r\n                                            services will be\r\n                                            processed for the purposes\r\n                                            set out below.</li> <li>We collect your credit/debit card information to allow you to make purchases\r\n                                            of goods / services on\r\n                                            the Platform. All\r\n                                            credit/debit cards details and Personal Information will NOT be stored,\r\n                                            sold, shared, rented or\r\n                                            leased to any third\r\n                                            parties.</li> <li>We collect your image and voice for the purposes of promoting you as the\r\n                                            winner of any Prize Draws.\r\n                                        </li> <li>Most of the Platform can be used without giving Us any information. However,\r\n                                            certain services may\r\n                                            involve the collection\r\n                                            of information from you. We use this information to provide you with a\r\n                                            better service, and in\r\n                                            particular to: keep\r\n                                            internal records; improve Our products, services and the Platform;\r\n                                            communicate with you if you have\r\n                                            ordered, purchased\r\n                                            or participated in anything on the Platform; contact you for market research\r\n                                            purposes; and provide\r\n                                            you with information\r\n                                            about new opportunities, promotions, special offers and other information\r\n                                            that We may feel is\r\n                                            relevant to you. If you\r\n                                            contact Us via the Platform, or via email to the contacts set out on the\r\n                                            Platform, We may keep a\r\n                                            record of that\r\n                                            correspondence.</li> <li>We collect general information to help Us make the Platform more useful to\r\n                                            users (including to help\r\n                                            Us assess content of\r\n                                            interest to visitors), and for other purposes such as determining the\r\n                                            Platform's technical design\r\n                                            specifications and\r\n      