   this privacy policy and\r\n                                            you should check\r\n                                            the privacy policies of these third parties.</li></ol></li> <li>NON-DISCLOSURE OF PERSONAL INFORMATION\r\n                                    <ol><li>You agree that We may share your Personal Information within Our group\r\n                                            entities and subsidiaries\r\n                                            (together the\r\n                                            &quot;Company&quot;). We will not share, sell or rent your Personal Information in any\r\n                                            manner to any third\r\n                                            parties outside of Our\r\n                                            Company except when We have your consent under this privacy policy; or if\r\n                                            required to do so by law\r\n                                            or any governing\r\n                                            authority. For example, We may share your Personal Information with Our\r\n                                            third party service\r\n                                            providers, with brands for\r\n                                            advertising and marketing purposes, and with third parties for data storage\r\n                                            purposes, and within Our\r\n                                            Company.</li> <li>We may transfer the Personal Information you provide to Us to another\r\n                                            country. For example, we may\r\n                                            store your Personal\r\n                                            Information in a data centre in France. When we transfer your Personal\r\n                                            Information to another\r\n                                            country, we will take all\r\n                                            reasonable steps to ensure that your Personal Information is protected in\r\n                                            accordance with this\r\n                                            privacy policy and\r\n                                            applicable data protection laws, including through using legally recognised\r\n                                            data transfer\r\n                                            mechanisms, such as\r\n                                            appropriate standard contractual clauses where required.</li></ol></li> <li>SECURITY\r\n                                    <ol><li>Keeping your Personal Information secure and preventing unauthorized access\r\n                                            is of utmost priority to\r\n                                            Us, and We take all\r\n                                            steps reasonably necessary to protect your Personal Information against any\r\n                                            unauthorised access,\r\n                                            use, alteration,\r\n                                            disclosure or destruction. Whilst We have put in place physical, electronic\r\n                                            and managerial\r\n                                            procedures to secure and\r\n                                            safeguard your Personal Information, We will not be held responsible for any\r\n                                            unauthorised access by\r\n                                            third parties and We\r\n                                            cannot guarantee that the Personal Information provided by you or that is\r\n                                            transmitted via the\r\n                                            Platform or by e-mail is\r\n                                            totally secure and safe.\r\n                                        </li> <li>We encourage you to keep any passwords you use confidential and to be\r\n                                            careful to avoid &quot;phishing&quot;\r\n                                            scams where someone\r\n                                            may send you an electronic message that appears to be from Us asking for\r\n                                            your Personal Information.\r\n                                            We will not request\r\n                                            your ID or password through electronic messages.\r\n                                        </li></ol></li> <li>USE OF COOKIES\r\n                                    <ol><li>Like many websites, the Platform uses cookies, which are files that store\r\n                                            information on your hard\r\n                                            drive. Cookies allow\r\n                                            the Platform to identify account holders, track new users, record session\r\n                                            information, and generally\r\n                                            to optimise your\r\n                                            experience. Most browsers accept cookies automatically but if you choose,\r\n                                            you can disable the\r\n                                            cookies from your browser.\r\n                                            For online advertising campaigns, cookies are automatically deleted after\r\n                                            one (1) month.\r\n                                        </li> <li>We recommend that you leave cookies &quot;turned on&quot; so that you can enjoy a\r\n                                            better experience on the\r\n                                            Platform. You may\r\n                             