form.\r\n                                            This means that third\r\n                                            parties may use cookies,\r\n                                            web beacons, and similar technologies to collect or receive information from\r\n                                            the Platform and\r\n                                            elsewhere on the internet\r\n                                            and use that information to provide measurement services and targeted\r\n                                            adverts. If you wish to opt\r\n                                            out of the use of\r\n                                            these tracking pixels, please adjust your browser settings.\r\n                                        </li></ol></li> <li>\r\n                                    YOUR MARKETING PREFERENCES\r\n                                    <ol><li>You may choose to receive or not receive marketing communications from Us by\r\n                                            indicating your\r\n                                            preferences.</li> <li>You can click &quot;unsubscribe&quot; in any marketing electronic communications We\r\n                                            send you, or by sending Us\r\n                                            an email at <a href=\"mailto:info@Lottery.com\">info@Lottery.com</a>.</li> <li>On the Platform, you can choose whether you wish to receive marketing\r\n                                            communications from Us.</li> <li>Any marketing by Us, or any third parties on Our behalf, will be conducted\r\n                                            in accordance with\r\n                                            applicable laws and\r\n                                            include (where applicable) methods to allow you to express your preferences\r\n                                            (including the\r\n                                            possibility of being removed\r\n                                            from Our advertising and marketing lists as set out above).</li></ol></li> <li>\r\n                                    YOUR RIGHTS\r\n                                    <ol><li>Where the processing of your Personal Information is subject to data\r\n                                            protection laws, you have\r\n                                            certain data subject\r\n                                            rights. Depending on the applicable law, these include:</li> <li>Access: you have the right to request information about how We process your\r\n                                            Personal Information.\r\n                                        </li> <li>Rectification: you have the right to request the rectification of inaccurate\r\n                                            Personal Information\r\n                                            about you and for any\r\n                                            incomplete Personal Information about you to be completed.\r\n                                        </li> <li>Transfer: you have the right to request a transfer of your Personal\r\n                                            Information.\r\n                                        </li> <li>Erasure: you have the right to have your Personal Information erased.</li> <li>Restriction: you have the right to restrict the processing of your Personal\r\n                                            Information in some\r\n                                            cases.</li> <li>Objection: you have the right to object to processing in some circumstances,\r\n                                            including for automated\r\n                                            processing.</li> <li>In addition to the above, you have the right to withdraw your consent to the\r\n                                            processing of your\r\n                                            Personal Information at\r\n                                            any time. Such withdrawal has no effect on any processing of your Personal\r\n                                            Information that has\r\n                                            occurred prior to the\r\n                                            withdrawal of your consent.</li> <li>If you have any questions about the type of Personal Information We hold\r\n                                            about you or wish to\r\n                                            exercise any of your data\r\n                                            subject rights, please send a written request to <a href=\"mailto:info@Lottery.com\">info@Lottery.com</a>.\r\n                                        </li></ol></li> <li>HOW LONG WE KEEP YOUR PERSONAL INFORMATION\r\n                                    <ol><li>We will retain your Personal Information for the length of time needed to\r\n                                            fulfil the purposes\r\n                                            outlined in this privacy\r\n                                            policy unless a longer retention period is required or permitted by law.\r\n                                        </li></ol></li></ol></div></div></div></div></div></section> "),_c('Footer')],2)
